.post-container {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding-right: 10%;
  padding-left: 10%;
  margin: 50px 0 50px 0;
}
.postPage-container .postPage_image {
  width: 100%;
  cursor: initial;
}
.postPage_title_container {
  display: flex;
  width: 100%;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding-bottom: 50px;
}
.postPage_title {
  font-size: 2.5rem;
  font-family: "Montserrat", sans-serif;
  font-weight: 800;
  text-align: center;
  color: #003a45;
  opacity: 0.9;
}
.pagePost-description {
  padding-top: 40px;
  width: 100%;
  height: 100px;
  padding-bottom: 30px;
  line-height: 1.5;
  overflow: hidden;
  color: #003a45;
}
.postPage-date {
  width: 150px;
  display: flex;
  flex-direction: column;
  margin-top: 20px;
}
.postPage-date p {
  margin-top: 2px;
  color: #003a45;
  opacity: 0.9;
  font-family: "Roboto Condensed", sans-serif;
  font-weight: 300;
}
.postPage-date-clock {
  width: 100%;
  display: flex;
  align-items: center;
  line-height: 0;
}
.postPage-date-clock img {
  width: 18px;
  height: 18px;
  margin-right: 5px;
  opacity: 0.6;
}
.post-footer-socials img {
  width: 25px;
  height: 25px;
  margin-right: 10px;
  opacity: 0.5;
}

.post-footer {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}

.post-footer-socials {
  display: flex;
  align-items: center;
}

.post-footer-socials button {
  outline: none;
}

@media all and (max-width: 768px) {
    .post-container  {
    padding:0;
  }
}
